import { GetComponentProps } from 'topwrite';
import Zoom from 'react-medium-image-zoom';
import 'react-medium-image-zoom/dist/styles.css';
import '../scss/style.scss';

export default function({ children }: GetComponentProps<'html:img'>) {

    return <Zoom wrapElement={'span'} zoomMargin={50}>
        {children}
    </Zoom>;
}
